.home {
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
    grid-template-rows: 1fr 1fr .1fr;
    position: relative;
    background-color: var(--second);
    width: 100vw;
    height: 100vh;
    grid-template-areas: 
        'topcont top1 top2'
        'bottomcont moreabout bottom1'
        'footer footer footer';
}

.home .topcont {
    grid-area: topcont;
    text-align: center;
    display: grid;
    color: var(--first);
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-areas: 
        't'
        'm'
        'b';
}

.home .topcont h1{
    font-size: 5em;
}
.home .topcont h3{
    font-size: 3em;
}
.home .topcont h5{
    font-size: 1.2em;
}

.home .topcont .mid {
    grid-area: m;
    background-color: var(--second);
}

.home .topcont .bot {
    grid-area: b;
    background-color: var(--second);
}

.home .top1{
    grid-area: top1;
    background-color: var(--third);
    color: var(--second);
    transition: 0.3s;
    font-size: 3em;
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-areas: 
        'logo'
        'text';
}

.home .top1:hover {
    background-color: var(--second);
    color: var(--third);
}

.home .top1 img{
    filter: invert(18%) sepia(10%) saturate(1047%) hue-rotate(172deg) brightness(99%) contrast(89%);
}

.home .top1:hover img{
    transition: .3s;
    filter: invert(72%) sepia(13%) saturate(1279%) hue-rotate(173deg) brightness(88%) contrast(86%);
}

.home .top2 {
    grid-area: top2;
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-areas: 
        'top'
        'bottom';
}

.home .top2 .topLink {
    grid-area: top;
    font-size: 2.7em;
    transition: .3s;
    color: var(--prime);
    background-color: var(--second);
}

.home .top2 .topLink:hover {
    background-color: var(--prime);
    color: var(--second);
}

.home .top2 .topLink:hover img {
    transition: .3s;
    filter: invert(18%) sepia(10%) saturate(1047%) hue-rotate(172deg) brightness(99%) contrast(89%);
}

.home .top2 .bottomLink {
    background-color: var(--second);
    grid-area: bottom;
    transition: .3s;
    font-size: 2.7em;
    color: var(--prime);
}

.home .top2 .bottomLink:hover {
    background-color: var(--prime);
    color: var(--second);
}

.home .top2 .bottomLink:hover img {
    transition: .3s;
    filter: invert(18%) sepia(10%) saturate(1047%) hue-rotate(172deg) brightness(99%) contrast(89%);
}

.home .bottomcont {
    grid-area: bottomcont;
    text-align: center;
    display: grid;
    font-size: 1.2em;
    background-color: var(--second);
    grid-template-rows: .75fr 1fr .75fr;
    grid-template-areas: 
        'contenttop'
        'contentmid'
        'contentbot';
}

.home .bottomcont .content {
    grid-area: contentmid;
    color: var(--subtext);
}


.home .moreabout {
    grid-area: moreabout;
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-areas: 
        'about'
        'cv';
}


.home .moreabout .about {
    grid-area: about;
    font-size: 2.7em;
    transition: .3s;
    background-color: var(--second);
    text-decoration: none;
    color: var(--prime);
}

.home .moreabout .about:hover {
    background-color: var(--prime);
    color: var(--second);
}

.home .moreabout .about img {
    transition: .3s;
    filter: invert(87%) sepia(22%) saturate(568%) hue-rotate(190deg) brightness(109%) contrast(105%);
}

.home .moreabout .about:hover img {
    transition: .3s;
    filter: invert(18%) sepia(10%) saturate(1047%) hue-rotate(172deg) brightness(99%) contrast(89%);
}

.home .moreabout .cv {
    grid-area: cv;
    font-size: 2.7em;
    transition: 0.3s;
    background-color: var(--second);
    text-decoration: none;
    color: var(--prime);
}

.home .moreabout .cv:hover {
    background-color: var(--prime);
    color: var(--second);
    transition: .3s;
}

.home .moreabout .cv img{
    transition: .3s;
    filter: invert(87%) sepia(22%) saturate(568%) hue-rotate(190deg) brightness(109%) contrast(105%);
}

.home .moreabout .cv:hover img{
    transition: .3s;
    filter: invert(18%) sepia(10%) saturate(1047%) hue-rotate(172deg) brightness(99%) contrast(89%);
}

.home .bottom1 {
    grid-area: bottom1;
    background-color: var(--prime);
    color: var(--second);
    transition: .3s;
    font-size: 3em;
    text-decoration: none;
}

.home .bottom1:hover img{
    transition: .3s;
    filter: invert(100%);
}

.home .bottom1:hover {
    background-color: var(--second);
    color: var(--prime);
}

.home footer {
    grid-area: footer;
    text-align: center;
    background-color: var(--fourth);
    display: grid;
    font-size: 1.2em;
    grid-template-rows: .75fr 1fr .75fr;
    grid-template-areas: 
        'footertop'
        'footermid'
        'footerbot';
}

.home .footer {
    grid-area: footermid;
}

.page {
    background-color: var(--second);
    color: white;
    font-family: 'Comfortaa';
    position: relative;
    width: 100vw;
    height: 100vh;
}


/* Smartphones (portrait and landscape) ----------- */
@media only screen 
and (min-device-width : 320px) 
and (max-device-width : 480px) {
    .home {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr .1fr;
        grid-template-areas: 
            'topcont'
            'bottomcont'
            'top1'
            'top2'
            'moreabout'
            'bottom1'
            'footer';
    }
}

